<template>
  <div>
    <div class="container mt-3 mb-5">

      <div class="my-3">
        <h3 v-if="parametro == 0"><i class="fas fa-file-invoice-dollar"></i> registrar cotización</h3>
        <h3 v-if="parametro > 0"><i class="fas fa-file-invoice-dollar"></i> actualizar la cotización</h3>
      </div>

      <hr />

      <!-- Vendedor, Descuento -->
      <b-row>

        <!-- Vendedor -->
        <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
          <label>Vendedor</label>
          <div class="input-group">
            <b-form-select
              id="cboVendedor"
              v-model="cotizacion.codVendedor"
              :options="vendedores"
              v-bind:class="{ hasError: hasErrorVendedor }"
              v-on:change="obtenerClientes"
              :disabled="disabledVendedor || disabledCotizacion"
            >
            </b-form-select>
          </div>
        </div>

        <!-- Descuento -->
        <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2">
          <b-form-checkbox
            v-model="cotizacion.tieneDescuento"
            name="check-button"
            class="col-2 mb-2"
            checkbox
            size="md">
            Descuento
          </b-form-checkbox>

          <div class="row" v-if="cotizacion.tieneDescuento">
            <div class="input-group">
              <input v-selectonfocus
                  type="text"
                  class="form-control col-3 ml-3"
                  v-bind:class="{ hasError: hasErrorDescuento }"
                  v-on:blur="calcularDescuento"
                  @keypress.enter="calcularDescuento"
                  v-model="cotizacion.porcentajeDescuento"
                  v-on:change="hasErrorDescuento = false"
                  >
              <div class="input-group-append">
                <span class="input-group-text" id="basic-addon2">%</span>
              </div>
            </div>
          </div>
        </div>

      </b-row>

      <!-- Cliente, Obras -->
      <b-row>
        <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2 mt-2">
          <label>Cliente</label>
          <div class="input-group">
            <b-form-select
              id="cboCliente"
              :disabled="disabledClientes || disabledCotizacion"
              v-model="cotizacion.codCliente"
              :options="clientes"
              v-bind:class="{ hasError: hasErrorCte }"
              v-on:change="changeCliente"
            >
            </b-form-select>
          </div>
        </div>

        <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2 mt-2">
          <label>Obras</label>
          <div class="input-group">
            <b-form-select
              id="cboObras"
              :disabled="disabledObras || disabledCotizacion"
              v-model="cotizacion.codObra"
              :options="obras"
              v-bind:class="{ hasError: hasErrorObra }"
              v-on:change="obraSeleccionada"
            >
            </b-form-select>
          </div>
          <!-- <b-form-text id="input-live-help">
						Dist. Apróx: {{distanciaActual}} <b>Km.</b>
						<br/>
						Tiempo Apróx: {{tiempoActual}}
				</b-form-text> -->
        </div>
      </b-row>

      <!-- Otro cliente / Otra obra -->
      <b-row v-if="cotizacion.codCliente == -1 || cotizacion.codObra == -1">

        <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2" v-if="cotizacion.codCliente == -1">
          <label>Otro Cliente</label>
          <div class="input-group">
            <input type="text" class="form-control" v-model="cotizacion.otroCliente" v-bind:class="{ hasError: hasErrorCte }"/>
          </div>
        </div>

        <div class="form-group col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-2" v-if="cotizacion.codObra == -1">
          <label>Otra Obra</label>
          <div class="input-group">
            <input type="text" class="form-control" v-model="cotizacion.otraObra" v-bind:class="{ hasError: hasErrorObra }"/>
          </div>
        </div>

      </b-row>

      <!-- Buscar productos -->
      <b-row class="d-flex justify-content-between">
        <div class="col-sm-12 col-md-4 col-lg-2 mb-2">
          <button class="btn btn-warning mb-2 mt-2" @click="buscarProducto">Buscar producto</button>
        </div>
      </b-row>

      <b-row v-if="listaProductos.length > 0" class="m-1">
        <table class="table table-striped table-sm " id="tablaClientes">
            <thead class="table-primary">
              <th>Código</th>
              <th>Producto</th>
              <th>Cantidad</th>
              <th>Precio m³</th>
              <th></th>
            </thead>
            <tbody ref="tablaClientesBody">
              <tr :key="index" :id="'row' + index" v-for="(producto, index) in listaProductos">
                <td v-text="producto.codProducto"></td>
                <td v-text="producto.descripcion"></td>
                <td v-text="producto.cantidad"></td>
                <td v-text="producto.precioOriginal"></td>
                <td>
                  <button @click="borrarProducto(index)" class="btn btn-danger btn-sm"><i class="fas fa-trash"></i></button>
                </td>

              </tr>
            </tbody>
          </table>
      </b-row>

      <hr />

      <!-- Guardar y Cancelar -->
      <b-row>
        <b-col class="col-md-6 col-sm-12 col-12" align-self="end"></b-col>
        <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
          <b-button @click="guardarCotizacion" block variant="success" class="mr-2 mt-2">
            <i class="fa fa-save"></i> Guardar
          </b-button>
        </b-col>
        <b-col class="col-md-3 col-sm-12 col-12" align-self="end">
          <b-button @click="cancelar" block variant="danger" class="mr-2 mt-2">
            Cancelar
          </b-button>
        </b-col>
      </b-row>

    </div>


    <!-- Modal buscar producto -->
    <b-modal v-model="modalProductos" centered size="lg" title="Agregar un nuevo producto a la cotización">

      <b-container fluid>

        <div class="row">

          <!-- Producto / Formula -->
          <div class="form-group col-sm-12 mb-1">
            <label>Fórmula</label>
            <div class="row">
              <div class="input-group col-xl-10">
                <input
                  ref="refFormula"
                  type="text"
                  class="form-control"
                  autocomplete="off"
                  v-model="state"
                  @input="filterState"
                  :disabled="disabledCotizacion"
                  @focus="focusInputFormula"
                  v-bind:class="{ hasError: hasErrorProducto }"
                />
                <span class="input-group-btn">
                  <b-button
                    :disabled="disabledFormula || disabledCotizacion"
                    variant="outline-primary"
                    class="input-group-4"
                    @click="limpiarProducto"
                    ><i class="fa fa-times" aria-hidden="true"></i
                  ></b-button>
                </span>
              </div>
              
            </div>
            <div v-if="formulasFiltradas && modal">
              <ul class="list-group items-body">
                <li
                  class="list-group-item py-2 items-body-content"
                  v-for="formula in formulasFiltradas"
                  v-bind:key="formula.value"
                  @click="setState(formula)"
                >
                  <span> {{ formula.text }}</span>
                  <i class="fa fa-angle-right"></i>
                </li>
              </ul>
            </div>
            <transition name="fade">
              <label v-show="mostrarProducto">
                <span class="labelSize">Nomenclatura: </span>
                {{ cotizacion.producto }}</label
              >
            </transition>
          
          </div>

          <!-- Cantidad -->
          <div class="form-group col-sm-12 mb-2">
            <label>Cantidad</label>
            <div class="input-group">
              <input
                id="cantidad"
                v-selectonfocus
                ref="refCantidad"
                type="text"
                class="form-control col-sm-4 col-md-3"
                v-bind:class="{ hasError: hasErrorCantidad }"
                v-model="cotizacion.cantidad"
                v-on:change="hasErrorCantidad = false"
                @focus="onFocus(1)"
                @keypress="numerosDecimales"
                :disabled="disabledCotizacion"
              />
              <label for="cantidad" class="labelSize col-4"><p>m<sup>3</sup></p></label>
            </div>
          </div>

          <!--Precio -->
          <div class="form-group col-sm-12">
            <div class="row">

              <div class="form-group col-sm-12 col-md-5">
                <label>Precio por m³ original</label>
                <input
                  type="text"
                  disabled
                  class="form-control col-sm-12"
                  v-model="cotizacion.precioOriginal"
                />
              </div>

              <div class="form-group col-sm-12 col-md-7 mb-2"
                v-if="cotizacion.tieneDescuento">
                <label>Precio por m³ con descuento</label>
                <input
                  type="text"
                  disabled
                  class="form-control col-sm-12 col-md-9"
                  v-model="cotizacion.precioDescuento"
                />
              </div>
              <div class="form-group col-sm-12 my-2 text-center">
              <label class="mr-3">
                <span>* El precio que se otorgue, afectará directamente a facturación. *</span>
              </label>
              </div>
            </div>
          </div>
          
        </div>

      </b-container>
      
      <template #modal-footer>
        <div class="w-100">
          <b-button
            variant="primary"
            class="float-right"
            @click="agregarProductoCotizacion">
            Agregar producto
          </b-button>
        </div>
      </template>

    </b-modal>
    
  </div>
</template>

<script>

import servicioDosificador from "@/services/servicio-dosificador.js";
import servicioClientes from "@/services/servicio-clientes";
import servicioAgente from "@/services/servicio-agentes";
import servicioCotizaciones from "@/services/servicio-cotizaciones";
// import servicioPlanta from "@/services/servicio-planta";

import Swal from "sweetalert2";

export default {
  data() {

    return {
      mostrarDescuento:false,
      hasErrorCte: false,
      hasErrorObra: false,
      hasErrorVendedor: false,
      hasErrorCantidad: false,
      hasErrorDescuento: false,
      disabledObras: true,
      disabledClientes: false,
      mostrarOperadores: true,
      distanciaActual: "0",
      tiempoActual: "0",
      clientes: [{ value: 0, text: "Seleccione el cliente" }],
      obras: [{ value: 0, text: "Seleccione la obra del cliente" }],
      vendedores: [{ value: 0, text: "Seleccione el vendedor" }],
      plantas: [{ value: 0, text: "Seleccione una planta" }],
      cotizacion: {
        folioCotizacion: 0,
        codCliente: 0,
        codObra: 0,
        codVendedor: 0,
        cantidad: 0,
        cantidadTotal: 0,
        producto: "",
        codProducto: 0,
        estatus: "",
        tieneDescuento: false,
        porcentajeDescuento: 0,
        observacion: "",
        codPlanta: 0,
        tieneFibra: false,
        tieneImper: false,
        precioOriginal: 0,
        precioDescuento: 0,
        bombeado: false,
        otroCliente: "",
        otraObra: "",
        productos: [],
      },

      inputSeleccionado: 0,
      parametro: 0,

      disabledFormula: true,
      nuevaFormula: false,
      mostrarProducto: false,
      state: "",
      formulasFiltradas: [],
      formulas: [],

      formulaNueva: "",
      hasErrorProducto: false,
      disabledlimpiarProductos: false,
      modal: false,

      disabledVendedor: false,
      disabledCotizacion: false,
      continuarGuardado: false,
      disabledPlanta: false,
      disabledPrecio: false,
      // disabledClientes

      modalProductos: false,
      listaProductos: [],
      folioCotizacion: 0,
    };
  },

  components: {
  },

  watch: {
    state: function (val) {
      val.length >= 1 && this.cotizacion.codProducto == 0
        ? (this.modal = true)
        : (this.modal = false);

      if (val.length === 0) {
        this.cotizacion.producto = 0;
        this.cotizacion.codProducto = 0;
        this.mostrarProducto = false;
        this.precios = [];
      }
    },
  },

  async mounted() {

    await this.cargarFormulas(0);

    this.parametro = this.$route.params.id != undefined ? this.$route.params.id : 0;

    if (this.parametro > 0) {
      await this.cargaCotizacion();
    }
  },

  async created() {
    // await this.obtenerClientes();
    // this.obtenerPlantas();

    if (this.usuario.agente === 1) {
      this.disabledVendedor = true;
      this.vendedores = [{ value: this.usuario.codEmpleado, text: this.usuario.nombre }];
      this.cotizacion.codVendedor = this.vendedores[0].value;
      this.obtenerClientes();
    } else {
      this.disabledVendedor = false;
      await this.obtenerVendedores();
      this.disabledClientes = true;
    }
    
    
  },

  computed: {},

  methods: {
    //clientes
    calcularDescuento() {
      this.cotizacion.precioDescuento = (
        this.cotizacion.precioOriginal -
        (this.cotizacion.porcentajeDescuento / 100) * this.cotizacion.precioOriginal
      ).toFixed(2);
    },
    async obtenerClientes() {
      this.hasErrorVendedor = false;
      this.hasErrorCte = false;
      this.hasErrorObra = false;

      // this.cotizacion.tieneDescuento = false;
      // this.cotizacion.porcentajeDescuento = "";

      this.clientes = [{ value: 0, text: "Seleccione el cliente" }];
      this.cotizacion.codCliente = this.clientes[0].value;
      this.obras = [{ value: 0, text: "Seleccione la obra del cliente" }];
      this.cotizacion.codObra = this.obras[0].value;
      this.disabledObras = true;

      if (this.cotizacion.codVendedor > 0) {
        // this.clientes = [{ value: 0, text: "Seleccione el cliente" }];
        this.$loading(true);
        await servicioClientes
          .obtenerClientesAgente(this.cotizacion.codVendedor)
          .then((r) => {
            if (r.value) {
              r.data.forEach((element) => {
                let tipo = { value: element.codigo, text: element.nombreCompleto };
                this.clientes.push(tipo);
              });

              let otro = { value: -1, text: "Otro" };
              this.clientes.push(otro);

            }
          });
        this.disabledClientes = false;
        this.$loading(false);
      } else {
        this.disabledClientes = true;
      }
    },

    async obtenerPrecios(cliente, formula) {
      this.$loading(true);
      await servicioDosificador
        .obtenerFormulas(formula)
        .then((r) => {
          this.$loading(false);
          if (r.value) {
            if (this.cotizacion.tieneDescuento) {
              this.cotizacion.precioOriginal = r.data[0].precioMaximo;
              this.cotizacion.precioDescuento = (
                this.cotizacion.precioOriginal -
                (this.cotizacion.porcentajeDescuento / 100) * this.cotizacion.precioOriginal
              ).toFixed(2);
            } else {
              this.cotizacion.precioOriginal = r.data[0].precioMaximo.toFixed(2);
            }
          }
        })
        .catch((error) => {
          this.$loading(false);
          console.error(error);
        });
    },
    async changeCliente(){
      await this.obtenerDescuentoCliente();
      await this.obtenerObras();
    },
    async obtenerDescuentoCliente(){
      let r = await servicioCotizaciones.obtenerUltimoDescuentoCliente(this.cotizacion.codVendedor, this.cotizacion.codCliente)
      if(r.value){
         let msgResult = await Swal.fire({
          title: "¡ATENCIÓN!",
          text: `El ultimo descuento asignado al cliente fue de ${r.data[0].porcentajeDescuento}%, ¿Deseas aplicarlo?`,
          icon: "question",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Aceptar",
          cancelButtonText: "Cancelar",
        });
        if(msgResult.value){
          this.cotizacion.tieneDescuento = true;
          this.cotizacion.porcentajeDescuento = r.data[0].porcentajeDescuento;
        }else{
          this.cotizacion.tieneDescuento = false;
          this.cotizacion.porcentajeDescuento = 0;
        }
      }
      this.mostrarDescuento = true;
    },
    async obtenerObras() {
      this.hasErrorObra = false;
      this.cotizacion.otraObra = "";
      let codCliente = this.cotizacion.codCliente;
      this.cotizacion.codObra = 0;
      this.obras = [{ value: 0, text: "Seleccione la obra del cliente" }];
      this.$loading(true);
      await servicioDosificador.obtenerObras(codCliente).then((r) => {
        this.$loading(false);
        if (r.value) {
          this.disabledObras = false;
          this.hasErrorCte = false;

          r.data.forEach((element) => {
            let tipo = { value: element.codigo, text: element.calleNumero };
            this.obras.push(tipo);
          });

          let otra = { value: -1, text: "Otra" };
          this.obras.push(otra);

        } else {
          this.disabledObras = true;
        }
      });
      this.$loading(false);
    },

    obraSeleccionada() {
      this.hasErrorObra = false;
    },

    // vendedores
    async obtenerVendedores() {
      this.$loading(true);
      await servicioAgente.agentesCombo().then((r) => {
        if (r.value) {
          r.data.forEach((element) => {
            let agente = {
              value: element.codVendedor,
              text: element.nombreVendedor,
            };
            this.vendedores.push(agente);
          });
        }
      });
      this.$loading(false);
    },

    // guardar
    async guardarCotizacion() {
      if (!this.validarRegistros()) {
        let r = await Swal.fire({
          title: "¡ATENCIÓN!",
          text: "¿Desea guardar la cotización?",
          showCancelButton: true,
          icon: "question",
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Aceptar",
          cancelButtonText: "Cancelar",
        });

        if (r.value) {
          this.continuarGuardado = true;
        }
      } else {

        if(this.listaProductos.length <= 0) {
          this.$swal.fire({
            text: "No hay productos agregados a la cotización.",
            icon: "error",
          });

        } else {
          this.$swal.fire({
            text: "Hay campos requeridos por completar",
            icon: "error",
          });
        }
        
      }

      if (this.continuarGuardado) {
        await this.CapturarObservacion();
        if (this.continuarGuardado) {
          console.log("guardo");
        }

        this.cotizacion.productos = this.listaProductos;

        this.$loading(true);
        let result = await servicioCotizaciones.guardarCotizacion(this.cotizacion);

        if (result.value) {

          this.folioCotizacion = result.data;
          this.$loading(false);

          await this.obtenerCotizacion();
          await Swal.fire("¡EXITO!", result.message, "success");
          this.$router.push({ path: "/agente-de-ventas/cotizaciones" });
        } else {
          this.$loading(false);
          this.$swal.fire({
            text: result.message,
            icon: "error",
          });
        }
      }
    },

    //cancelar
    async cancelar() {
      let r = await Swal.fire({
        title: "¡ATENCIÓN!",
        text: "¿Desea cancelar el registro de la cotización?",
        showCancelButton: true,
        icon: "question",
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });

      if (r.value) {
        window.history.back();
      }
    },

    validarRegistros() {
      var erroresEncontrados = false;
      if (this.cotizacion.codCliente <= 0) {

        if(this.cotizacion.otroCliente === "") {
          this.hasErrorCte = true;
          erroresEncontrados = true;
        } else {
          this.hasErrorCte = false;
        }
        
      }

      if (this.cotizacion.codObra <= 0) {

        if(this.cotizacion.otraObra === "") {
          this.hasErrorObra = true;
          erroresEncontrados = true;
        } else {
          this.hasErrorObra = false;
        }
        
      }

      if (this.cotizacion.codVendedor === 0) {
        this.hasErrorVendedor = true;
        erroresEncontrados = true;
      }

      if(this.listaProductos.length <= 0){
        erroresEncontrados = true;
      }

      if(this.cotizacion.tieneDescuento && this.cotizacion.porcentajeDescuento <= 0) {
        this.hasErrorDescuento = true;
        erroresEncontrados = true;
      }




      return erroresEncontrados;
    },

    inicializarDatos() {
      this.cotizacion = {
        codCliente: 0,
        codObra: 0,
        codVendedor: 0,
        cantidad: 0,
      };
    },

    numerosDecimales($event) {
      let keyCode = $event.keyCode ? $event.keyCode : $event.which;

      if (this.inputSeleccionado == 1) {
        if (
          (keyCode < 48 || keyCode > 57) &&
          (keyCode !== 46 || this.cotizacion.cantidad.indexOf(".") != -1)
        ) {
          // 46 is dot
          $event.preventDefault();
        }
      } 

    },

    onFocus: function (index) {
      this.inputSeleccionado = index;
    },

    async cargaCotizacion() {
      if (this.parametro > 0) {
        const now = new Date();
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
        var todayDate = today.toISOString().slice(0, 10);
        let codObra = 0;
        let codCliente = 0;
        let cliente = "", obra = "";
        this.$loading(true);

        await servicioCotizaciones
          .obtenerCotizaciones(this.parametro, todayDate, todayDate)
          .then((r) => {
            if (r.value) {

              console.log('R -> ', r);
              this.cotizacion.folioCotizacion = r.data[0].folioCotizacion;
              this.cotizacion.codVendedor = r.data[0].codVendedor;
              codCliente = r.data[0].codCliente;
              this.cotizacion.cantidad = r.data[0].cantidad;
              this.cotizacion.codProducto = r.data[0].codProducto;
              this.cotizacion.estatus = r.data[0].estatus;
              this.cotizacion.tieneDescuento = r.data[0].tieneDescuento;
              this.cotizacion.porcentajeDescuento = r.data[0].porcentajeDescuento;
              // this.cotizacion.tieneFibra = r.data[0].tieneFibra;
              // this.cotizacion.tieneImper = r.data[0].tieneImper;
              // this.cotizacion.bombeado = r.data[0].bombeado;
              codObra = r.data[0].codObra;
              cliente = r.data[0].cliente;
              obra = r.data[0].obra;
              // this.cotizacion.codPlanta = r.data[0].codPlanta;
              this.$loading(false);
            } else {
              this.$loading(false);
              this.$swal.fire({
                text: `${r.message}`,
                icon: "error",
              });
              this.$router.push({ path: "/agente-de-ventas/cotizaciones" });
            }
          });
        await this.obtenerClientes();
        this.cotizacion.codCliente = codCliente;
        await this.obtenerObras();
        // await this.cargarFormulas(0);
        this.cotizacion.codObra = codObra;

        if(codCliente < 0) {
          this.cotizacion.otroCliente = cliente;
        }

        if(codObra < 0) {
          this.cotizacion.otraObra = obra;
        }
        
        await this.CargarProductosCotizacion();
      }
    },

    // Formula

    // async agregarNvaFormula() {
    //   let r = await Swal.fire({
    //     title: "¿Desea crear una nueva fórmula de Producto?",
    //     showCancelButton: true,
    //     confirmButtonColor: "#3085d6",
    //     cancelButtonColor: "#d33",
    //     confirmButtonText: "Aceptar",
    //     cancelButtonText: "Cancelar",
    //   });

    //   if (r.value) {
    //     this.cotizacion.codProducto = 0;
    //     this.nuevaFormula = true;
    //     this.disabledProductos = true;
    //     this.$nextTick(() => {
    //       this.$refs.nvaFormula.focus();
    //     });
    //   }
    // },
    // async aceptarNuevaFormula() {
    //   this.$loading(true);
    //   await this.obtenerProducto(this.formulaNueva);
    //   let formula = {
    //     nomenclatura: this.formulaNueva,
    //     descripcion: this.cotizacion.producto,
    //   };
    //   await servicioDosificador.guardarFormula(formula).then((r) => {
    //     if (r.value && r.data != null) {
    //       let codFormulaNva = r.data[0];
    //       this.$swal.fire({
    //         text: r.message,
    //         icon: "success",
    //       });
    //       this.disabledProductos = false;
    //       this.cargarFormulas(0);
    //       this.cotizacion.codProducto = codFormulaNva;
    //     } else {
    //       this.$swal.fire({
    //         text: r.message,
    //         icon: "error",
    //       });
    //     }
    //     this.nuevaFormula = false;
    //   });
    //   this.$loading(false);
    // },

    async cargarFormulas(codigo) {
      this.$loading(true);
      let r = await this.obtenerFormulas(codigo);
      if (r.value) {
        r.data.forEach((element) => {
          let tipo = {
            value: element.codigo,
            text: `${element.nomenclatura} - ${element.descripcion}`,
            producto: element.nomenclatura,
          };
          this.formulas.push(tipo);
        });
      }
      this.$loading(false);
    },

    async obtenerFormulas(codigo) {
      return await servicioDosificador.obtenerFormulas(codigo);
    },

    filterState() {
      this.formulasFiltradas = this.formulas.filter((item) => {
        const descripcion = item.text.toString().toUpperCase();
        const nomenclatura = item.producto.toString().toUpperCase();
        const searchTerm = this.state.trim().toUpperCase();

        return descripcion.includes(searchTerm) || nomenclatura.includes(searchTerm);
      });
    },

    async setState(formula) {
      this.cotizacion.codProducto = formula.value;
      this.state = formula.text;
      this.cotizacion.producto = formula.producto;
      this.modal = false;
      this.mostrarProducto = true;
      this.hasErrorProducto = false;
      this.disabledFormula = true;
      await this.obtenerPrecios(this.cotizacion.codCliente, formula.value);
    },

    async obtenerFormulaProducto(idProducto) {
      let formula = await this.formulas.filter((c) => c.value == idProducto);
      await this.setState(formula[0]);
    },

    focusInputFormula() {
      if (this.state.length > 0) {
        this.modal = true;
      }
    },

    limpiarProducto() {
      this.cotizacion.producto = 0;
      this.cotizacion.codProducto = 0;

      // this.mostrarProducto = false;
      // this.disabledFormula = false;
      this.cotizacion.cantidad = 0;
      this.cotizacion.precioOriginal = "";
      this.cotizacion.precioDescuento = "";
      this.state = "";

      // this.cotizacion.otroCliente = "",
      // this.cotizacion.otraObra = ""
      // this.$nextTick(() => {
      //   this.$refs.refFormula.focus();
      // });
    },

    restablecerDatosModal() {
      this.cotizacion.producto = 0;
      this.cotizacion.codProducto = 0;
      this.cotizacion.cantidad = 0;
      this.cotizacion.precioOriginal = "";
      this.state = "";
      // this.cotizacion.otroCliente = "",
      // this.cotizacion.otraObra = ""
    },

    habilitarFormula() {
      this.disabledFormula = false;
      this.cotizacion.producto = 0;
      this.cotizacion.codProducto = 0;
      this.mostrarProducto = false;
      this.state = "";
      this.$nextTick(() => {
        this.$refs.refFormula.focus();
      });
      // this.disabledFormula = false;
      // this.cotizacion.producto = 0;
      // this.cotizacion.codProducto = 0;
      // this.mostrarProducto = false;
      // this.state = "";
      // this.$nextTick(() => {
      // 	this.$refs.refFormula.focus();
      // });
    },
    async CapturarObservacion() {
      let observacion;

      const { value: formValues } = await Swal.fire({
        title: "Ingrese una observación:",
        html:
          '<textarea id="swal-input2" class="swal2-input" placeholder="Capture una observación" style="margin-top: 20px; margin-bottom: 20px; height: 120px;">',
        focusConfirm: false,
        showCancelButton: false,
        confirmButtonText: "Aceptar",
        // cancelButtonText: 'Cancelar',
        confirmButtonColor: "#3085d6",
        // cancelButtonColor: '#d33',

        preConfirm: () => {
          observacion = Swal.getPopup().querySelector("#swal-input2").value;

          let inputObservacion = Swal.getPopup().querySelector("#swal-input2");

          inputObservacion.style.borderColor = "";

          // if (observacion === '') {
          // 	inputObservacion.style.borderColor = "red";
          // 	Swal.showValidationMessage(`Favor de ingresar una observación.`)
          // }
        },
      });

      if (formValues) {
        this.cotizacion.observacion = observacion;
        this.continuarGuardado = true;
      } else {
        this.continuarGuardado = false;
      }
    },

    buscarProducto() {
      this.modalProductos = true;
      this.limpiarProducto()
    },

    agregarProductoCotizacion() {

      if(this.cotizacion.codProducto > 0) {

        if(this.cotizacion.cantidad > 0) {

          let producto = { 
            codProducto: this.cotizacion.codProducto, 
            descripcion: this.state,
            cantidad: this.cotizacion.cantidad, 
            precioOriginal: this.cotizacion.tieneDescuento ? this.cotizacion.precioDescuento : this.cotizacion.precioOriginal
          };

          this.listaProductos.push(producto)

          this.restablecerDatosModal();
        
          this.modalProductos = false

        } else {
          this.$swal.fire({
              text: "Tiene que ingresar la cantidad de metros.",
              icon: "warning",
            });
        }


      } else {
        this.$swal.fire({
            text: "Tiene que seleccionar un producto.",
            icon: "warning",
          });
      }
    },

    async borrarProducto(index) {

      let r = await Swal.fire({
        text: "¿Desea eliminar el producto de la cotización?",
        title: "¡ATENCIÓN!",
        showCancelButton: true,
        icon: "question",
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Aceptar",
        cancelButtonText: "Cancelar",
      });

      if (r.value) {
        this.listaProductos.splice(index, 1);
      }

    },

    async obtenerCotizacion(){
        this.$loading(true);
        // console.log('Aqui se imprime el rpt');
        let result = await servicioCotizaciones.obtenerPDFCotizacion(this.folioCotizacion);
        if(result.value) {
            await this.ImprimirCotizacion(result.data);
            this.$loading(false);
            const Toast = this.$swal.mixin({
                toast: true,
                position: 'top-end',
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
                didOpen: (toast) => {
                    toast.addEventListener('mouseenter', this.$swal.stopTimer)
                    toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                }
            })

            Toast.fire({
                icon: 'success',
                title: 'Se genero la cotizacion correctamente descargado.'
            });
        } else {
            // console.log(result);
            this.$loading(false);
            this.$swal.fire({
                text: result.message,
                icon: "warning"
            });
        }
    },
        
    async ImprimirCotizacion(pdfResult) {
        const downloadLink = document.createElement("a");
        const fileName = "Cotización";
        downloadLink.href = "data:application/pdf;base64," + pdfResult;
        downloadLink.download = fileName;
        await downloadLink.click();
    },

    async CargarProductosCotizacion() {

      this.$loading(true);
      
      let result = await servicioCotizaciones.cargarProductosCotizacion(this.cotizacion.folioCotizacion);
      if(result.value) {

        result.data.forEach(element => {

          let producto = { 
            codProducto: element.codigo, 
            descripcion: element.descripcion,
            cantidad: element.cantidad, 
            precioOriginal: element.precio
          };
          
          this.listaProductos.push(producto);

				});

      }
      this.$loading(false);
    }

  },
};
</script>

