import base from '@/services/servicio-base'

const servicio = {}
    // const key = "AIzaSyAbG_eyVgmdfq7xuPTvidPbj36Vf-Tfjnk";


//............................................................ 
//                   C O T I Z A C I O N E S
//............................................................
servicio.obtenerCotizaciones = (cotizacion, fechaDesde, fechaHasta) => {
    return base.get(`/cotizaciones/obtener-cotizaciones/${cotizacion}/${fechaDesde}/${fechaHasta}`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.guardarCotizacion = (cotizacion) => {
    return base.post(`/cotizaciones/guardar`, cotizacion, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(e => {
            console.log(e);
        })
}

servicio.obtenerUltimoDescuentoCliente = (codAgente, codCliente) => {
    return base.get(`/pedidos/obtener-descuento-cliente/${codAgente}/${codCliente}`)
        .then(r => {
            // console.log(r);
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.obtenerPDFCotizacion = (folioCotizacion) => {
    return base.get(`/cotizaciones/obtener-cotizacion/pdf/${folioCotizacion}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

servicio.cargarProductosCotizacion = (folioCotizacion) => {
    return base.get(`/cotizaciones/obtener-productos/${folioCotizacion}`)
        .then(r => {
            return r.data;
        })
        .catch(error => {
            return error;
        })
}

export default servicio