<template>


<div class="container mt-3">

    <!-- Ver detalles -->
    <b-modal :id="infoModal.id"
            :title="infoModal.title"
            ok-only
            @hide="resetInfoModal"
            :codigo="infoModal.codigo"
            hide-footer>
      <tr v-for="(value, key) in infoModal.content" :key="key">
        <th>{{ key }}</th>
        <!-- Hay que hacerlo más dinámico -->
        <td v-if ="key != 'Observación' && key != 'Observacion' ">{{ value }}</td>
        <td v-if ="key == 'Observación' || key == 'Observacion' " class="text-danger font-weight-bold">{{ value }}</td>
      </tr>
      <b-button v-show="false"
                class="mt-3"
                variant="outline-primary"
                block
                :to="rutaNuevo + '?id=' + idSeleccionado">
            <i class="fa fa-eye"></i> Más información
        </b-button>
    </b-modal>

    <div class="mt-3">
        <h3><i class="fas fa-file-invoice-dollar"></i> Cotizaciones</h3>
    </div>
    <b-row>
        <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
            <label>Desde:</label>
            <b-form-datepicker v-model="desde" class="mb-2" locale="es" size="sm"></b-form-datepicker>
        </div>
        <div class="form-group col-sm-12 col-md-6 col-lg-5 col-xl-4 mb-1">
            <label>Hasta:</label>
            <b-form-datepicker v-model="hasta" class="mb-2" locale="es" size="sm"></b-form-datepicker>
        </div>
    </b-row>
    <b-row class="d-flex justify-content-between">
        <div class="col-sm-12 col-md-4 col-lg-2 mb-2">
            <b-button @click="buscarCotizaciones" block variant="success" class="mr-2">
                <i class="fa fa-search"></i> Buscar
            </b-button>
        </div>
    </b-row>

    <!-- User Interface controls -->
    <b-row class="my-2">
        <b-col md="4">
            <b-form-group label="Filtrar"
                        label-cols-sm="1"
                        label-cols-md="2"
                        label-size="sm"
                        label-for="filterInput">
                <b-input-group size="sm">
                    <b-form-input v-model="filter"
                                type="search"
                                id="filterInput"
                                placeholder="Escribe el filtro..."
                                autocomplete="off">
                    </b-form-input>
                    <b-input-group-append>
                        <b-button :disabled="!filter" @click="filter = ''">
                            <i class="fas fa-times"></i>
                        </b-button>
                    </b-input-group-append>
                </b-input-group>
            </b-form-group>
        </b-col>
        <b-col md="3">
            <b-form-group label="Por página"
                        label-cols-sm="2"
                        label-cols-md="2"
                        label-cols-lg="4"
                        label-size="sm"
                        label-for="perPageSelect">
                <b-form-select v-model="perPage"
                            id="perPageSelect"
                            size="sm"
                            :options="pageOptions">
                </b-form-select>
            </b-form-group>
        </b-col>

        <!-- FALTA VERIFICAR COMO TOMAR LOS PERMISOS -->
        <b-col md="3" offset-md="2">
            <b-button @click="redirect"
                    variant="primary"
                    class="btn btn-sm py-1 mt-1 btn-block">
                <i class="fa fa-plus"></i> Nueva cotización
            </b-button>
        </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table small
            stacked="md"
            head-variant="dark"
            :hover="hover"
            :striped="striped"
            :bordered="bordered"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :filterIncludedFields="filterOn"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered">

        <template v-slot:cell(estatusCotizacion)="row">
            <b-badge variant="success" v-if="row.item.estatus === 'A'" class="p-2">
                Aceptada
            </b-badge>
            <b-badge variant="secondary" v-if="row.item.estatus === 'P'" class="p-2">
                Pendiente
            </b-badge>
            
            <b-badge variant="danger" v-if="row.item.estatus === 'R'" class="p-2">
                Rechazada
            </b-badge>
        </template>



        <template v-slot:cell(actions)="row">
            <b-button @click="modal(row.item, row.index, $event.target)"
                    size="sm"
                    class="mr-1"
                    v-b-tooltip.hover
                    title="Ver Detalle"
                    variant="outline-info">
                <i class="fa fa-eye"></i>
            </b-button>

            <b-button :to="rutaNuevo + row.item.folioCotizacion"
                        :disabled="(row.item.estatus == 'C')"
                        size="sm"
                        class="mr-1"
                        v-b-tooltip.hover
                        title="Editar"
                        variant="outline-info">
                <i class="fa fa-edit"></i>
            </b-button>
      </template>

      <template v-slot:cell(actionsReimprimir)="row">
            <b-button @click="obtenerCotizacion(row.item.folioCotizacion)"
                        variant="dark"
                        size="sm"
                        v-b-tooltip.hover
                        title="Reimprimir Cotización">
                <i class="fa fa-print mr-2"></i>Reimprimir
            </b-button>
            
        </template>

    </b-table>

    <b-pagination v-model="currentPage"
                    class="mt-1"
                    :total-rows="totalRows"
                    :per-page="perPage"
                    align="center"
                    aria-controls="my-table">
    </b-pagination>


</div>

</template>

<script>

// import Swal from 'sweetalert2'
import servicioCotizaciones from '@/services/servicio-cotizaciones';

export default {
    data(){
        const now = new Date()
        const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
        var todayDate = today.toISOString().slice(0,10);

        return{
            desde: todayDate,
            hasta: todayDate,
            items: [],

            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],

            fields: [
                { key: "folioCotizacion", label: "Cotización", sortable: true, sortDirection: "desc", },
                { key: "cliente", label: "Cliente", sortable: true, sortDirection: "desc", },
                // { key: "formatoCantidad", label: "Cantidad", sortable: true, class: "text-center" },
                // { key: "nomenclatura", label: "Producto", sortable: true, class: "text-center" },
                { key: "fechaRecepcion", label: "Fecha Envio", sortable: true, class: "text-center" },
                // { key: "horaSalidaYPlanta", label: "Hora Salida", sortable: true, class: "text-center" },
                { key: "estatusCotizacion", label: "Estatus", sortable: true, class: "text-center" },
                { key: "actions", label: "", class: "text-center" },
                { key: "actionsReimprimir", label: "", class: "text-center" },


            ],
            permisoEscritura: true,
            rutaNuevo: "cotizaciones/registro/",
            modalShow: false,

            headerBgVariant: "dark",
            headerTextVariant: "light",
            
            infoModal: {
                id: "info-modal",
                title: "",
                content: "",
                codigo: "",
            },
            idSeleccionado: 0,
        }
    },
    components: {
        // TableSortable,
    },
    async mounted() {
        await this.ObtenerCotizaciones();
        await this.CargarRenglones();
    },
    created() {
    },
    computed: {
        sortOptions() {
            // Create an options list from our fields
            return this.fields
                        .filter((f) => f.sortable)
                        .map((f) => {
                            return { text: f.label, value: f.key };
                        });
        },
    },
    methods: {

        onFiltered(filteredItems) {
            // Trigger pagination to update the number of buttons/pages due to filtering
            this.totalRows = filteredItems.length;
            this.currentPage = 1;
        },

        async buscarCotizaciones() {
            await this.ObtenerCotizaciones();
        },

        async ObtenerCotizaciones() {
            this.$loading(true);
            await servicioCotizaciones.obtenerCotizaciones(0, this.desde, this.hasta)
                            .then((r) => {
                                this.items = r.data;
                                this.$loading(false);
                            })
                            .catch((error) => {
                                console.log(error);
                            });
        },

        redirect() {
            setTimeout(() => {
                this.$router.push({ path: `${this.rutaNuevo}0` });
            }, 500);
        },

        CargarRenglones() {
            setTimeout(() => {
                this.totalRows = this.items.length ;
            }, 1000);
        },

        modal(item, index, button) {
            let content = "";
            this.idSeleccionado = item.codigo;
            //Checo si la ultimaParteLink viene en el metodo
            this.infoModal.title = `Datos de la cotización #${item.folioCotizacion}`;
                content += `{ `;
                content += `"Folio Cotización" : ": ${item.folioCotizacion}\\n\\n", `;
                content += `"Cliente" : ":  ${item.cliente}\\n\\n",`;
                content += `"Fecha de Recepcion" : ":  ${item.fechaRecepcion}\\n\\n",`;
                // content += `"Hora de Salida" : ":  ${item.horaSalida}\\n\\n",`;
                content += `"Obra" : ":  ${item.obra}\\n\\n",`;
                content += `"Cantidad" : ":  ${item.cantidad} m³\\n\\n",`;
                // content += `"Cierre" : ":  ${item.cierre ? 'Si': 'No'}\\n\\n",`;
                // content += `"Cantidad Cierre" : ":  ${item.cantidadCierre} m³\\n\\n",`;
                content += `"Vendedor" : ":  ${item.vendedor}\\n\\n",`;
                content += `"Usuario" : ":  ${item.usuario}\\n\\n",`;
                content += `"Observación" : ":  ${item.observacion}\\n\\n",`;
                content += `"E-1" : ":  ${item.tieneFibra ? ' Fibra ' : 'N/A'} \\n\\n",`
                content += `"E-2" : ":  ${item.tieneImper ? ' Imper ' :'N/A'} \\n\\n"`;
                content += `}`;

                content = JSON.parse(content);
                this.infoModal.content = content;
                this.$root.$emit("bv::show::modal", this.infoModal.id, button);
        },

        resetInfoModal() {
            this.infoModal.title = "";
            this.infoModal.content = "";
        },

        async obtenerCotizacion(row){
            this.$loading(true);
            // console.log('Aqui se imprime el rpt');
            let result = await servicioCotizaciones.obtenerPDFCotizacion(row);
            if(result.value) {
                await this.ImprimirCotizacion(result.data);
                this.$loading(false);
                const Toast = this.$swal.mixin({
                    toast: true,
                    position: 'top-end',
                    showConfirmButton: false,
                    timer: 3000,
                    timerProgressBar: true,
                    didOpen: (toast) => {
                        toast.addEventListener('mouseenter', this.$swal.stopTimer)
                        toast.addEventListener('mouseleave', this.$swal.resumeTimer)
                    }
                })

                Toast.fire({
                    icon: 'success',
                    title: 'Se reimprimio la cotización correctamente.'
                });
            } else {
                // console.log(result);
                this.$loading(false);
                this.$swal.fire({
                    text: result.message,
                    icon: "warning"
                });
            }
        },
        
        async ImprimirCotizacion(pdfResult) {
            const downloadLink = document.createElement("a");
            const fileName = "Cotización";
            downloadLink.href = "data:application/pdf;base64," + pdfResult;
            downloadLink.download = fileName;
            await downloadLink.click();
        },

    }
}
</script>

<style scoped>



</style>






